/* eslint-disable react/jsx-no-target-blank */
import { useState, useEffect } from 'react'
import { Link, useParams } from 'react-router-dom'
import { inject, observer } from 'mobx-react'
import Claim from './Claim'
import { useAlert } from 'react-alert'
import OperateLiquidity from './OperateLiquidity'
import Tab from '../../components/Tab/Tab'
import DeriNumberFormat from "../../utils/DeriNumberFormat"
import Icon from '../../components/Icon/Icon'
import Search from '../../components/Search/Search'
import apiProxy from "../../model/ApiProxy"
import { formatAddress, isCakeLP, isSushiLP } from "../../utils/utils"
import UnderlineText from '../../components/UnderlineText/UnderlineText'
import config from '../../config.json'
import useConfig from '../../hooks/useConfig'
import { DeriEnv, openConfigListCache } from '../../lib/web3js/index'
import './detail.less'
import backIcon from './img/back.svg'
import BSC from '../../assets/img/bsc-logo.svg'
import POLYGON from '../../assets/img/polygon-logo.svg'
import ETH from '../../assets/img/ethereum.svg'
import downIcon from '../../assets/img/arrow-down.svg'
import stakedLogo from './img/staked.svg'
import errorimg from '../../assets/img/ErrorImg.png'
import addicon from './img/add-icon.svg'
import removeicon from './img/remove-icon.svg'
import inaddicon from './img/inactive-add.svg'
import inremoveicon from './img/inactive-remove.svg'
import Button from '../../components/Button/Button'
const chainConfig = config[DeriEnv.get()]['chainInfo']

const env = DeriEnv.get();
const { chainInfo } = config[env]
function Detail({ lang, wallet = {}, poolManager }) {
  const { version, chainId, address, type } = useParams();
  const [nowToken, setNowToken] = useState('')
  const [nowTokenId, setNowTokenId] = useState('')
  const [nowIsStaked, setNowIsStaked] = useState(false);
  const [poolConfig, setPoolConfig] = useState('')
  const [bTokenList, setBTokenList] = useState([])
  const [showBTokenList, setShowBTokenList] = useState([])
  const [xvsNum, setXvsNum] = useState()
  const [isShowInfo, setIsShowInfo] = useState('show')
  const [poolInfo, setPoolInfo] = useState({})
  const config = useConfig();
  const [symbols, setSymbols] = useState('')
  const [walletBalance, setWalletBalance] = useState()
  const [nowApy, setNowApy] = useState({})
  const networkText = chainInfo[chainId] && chainInfo[chainId].name;

  const networkLogo = (chainId) => {
    let src;
    let id = +chainId
    if (id === 56 || id === 97) {
      src = BSC
    } else if (id === 137) {
      src = POLYGON
    } else if (id === 1) {
      src = ETH
    }
    return src
  }
  const alert = useAlert();


  const onImgError = (event) => {
    event.target.src = errorimg
  }

  const getWalletBalance = async () => {
    if (wallet.isConnected()) {
      let params = [wallet.detail.chainId, address, wallet.detail.account]
      let balance = null;
      if (poolConfig.isLp) {
        balance = await apiProxy.request('getLpWalletBalance', params)
      } else {
        if (version === 'v3' || version === 'v3_lite') {
          params.push(nowToken)
        } else {
          params.push(nowTokenId)

        }
        if (version === 'v2_lite_open') {
          await openConfigListCache.update()
        }
        balance = await apiProxy.request('getBTokenBalance', params)
      }
      setWalletBalance(balance)
    }
  }
  const afterTransaction = () => {
    getWalletBalance()
    getLiquidityInfo()
    getIsStaked()
  }
  const tabSet = {
    [lang['add']]: {
      content: <OperateLiquidity poolConfig={poolConfig} isLp={type} wallet={wallet} afterTransaction={afterTransaction} version={version} bTokenId={nowTokenId} address={address} lang={lang} type='add' poolInfo={poolInfo} baseToken={nowToken} walletBalance={walletBalance} />,
      style: { background: "linear-gradient(200.74deg, rgba(97, 122, 255, 0.1) -4.65%, rgba(223, 229, 255, 0) 50.94%, rgba(85, 91, 237, 0.05) 105.4%)" },
      borderColor: "#203B60",
      icon: addicon,
      inicon: inaddicon,
      direction: "tab-left"
    },
    [lang['remove']]: {
      content: <OperateLiquidity isLp={type} poolConfig={poolConfig} wallet={wallet} afterTransaction={afterTransaction} lang={lang} address={address} type='remove' bTokenId={nowTokenId} poolInfo={poolInfo} balance={poolInfo} baseToken={nowToken} version={version} />,
      style: { background: "linear-gradient(200.74deg, rgba(97, 122, 255, 0.1) -4.65%, rgba(223, 229, 255, 0) 50.94%, rgba(85, 91, 237, 0.05) 105.4%)" },
      borderColor: "#203B60",
      icon: removeicon,
      inicon: inremoveicon,
      direction: "tab-right",
    },
  }
  const load = async () => {
    await poolManager.load()
    let pool = poolManager.pools;
    if (type === 'lp') {
      await poolManager.loadDoubleMingingPool();
      pool = poolManager.lpPools
    }
    if (version === 'v2_lite_open') {
      await poolManager.loadOpen();
      pool = poolManager.openPools
    }
    pool = pool.filter(p => p.address === address)
    pool = pool[0]
    let bToken0 = pool.bTokens[0].bTokenSymbol
    if (isCakeLP(address)) {
      bToken0 = 'PANCAKE-LP'
    } else if (isSushiLP(address)) {
      bToken0 = 'DERI-USDT SLP'
    }
    let bTokenId = pool.bTokens[0].bTokenId
    let symbols = pool.symbols.map(item => {
      let arr = [];
      arr.push(item.symbol)
      return arr
    })
    symbols = symbols.join(',')
    setSymbols(symbols)
    setPoolConfig(pool)
    setBTokenList(pool.bTokens)
    setNowToken(bToken0)
    setNowTokenId(bTokenId)
  }
  const Collect = async () => {
    let res = await apiProxy.request('claimXVSLp', [wallet.detail.chainId, address, wallet.detail.account], { includeResponse: true })
    if (!res || !res.success) {
      alert.error(lang['failure-of-transaction']);
    } else {
      alert.success(lang['collect-success'])
    }
    getVenusEarned()
  }
  const getPoolInfo = async () => {
    if (poolConfig) {
      let res = await poolConfig.getPoolInfo(wallet, poolConfig);
      if (res) {
        let arr = bTokenList.map(item => {
          if (version === 'v2') {
            item.apy = (+(res.bTokensApy.filter(i => i.bTokenId === item.bTokenId)[0].apy) * 100)
          } else if (version !== 'v2_lite_open' && version !== 'v3' && version !== 'v3_lite') {
            item.apy = +(res.bTokensApy[0].apy) * 100
            if (isSushiLP(address)) {
              item.lpApy = res.lpApy
            }
          } else if (version === 'v3' || version === 'v3_lite') {
            let apyArr = res.bTokensApy.find(i => i.bTokenSymbol === item.bTokenSymbol)
            if (apyArr) {
              item.apy = (+apyArr.apy) * 100
              item.supplyApy = (+apyArr.supplyApy) * 100
              item.xvsApy = (+apyArr.xvsApy) * 100
            }

          }
          return item
        })
      }

      return res
    }
  }
  const getLiquidityInfo = async () => {
    let info = null
    if (poolConfig) {
      let res = await poolConfig.getPoolInfo(wallet, poolConfig);
      let apy;
      let supplyApy;
      let xvsApy;
      let liquidity;
      let apyMuList;
      let multiplier;
      if (res.bTokensApy) {
        if (version === 'v2' && nowTokenId) {
          apyMuList = res.bTokensApy.filter(p => p.bTokenId === nowTokenId);
          apy = apyMuList[0].apy;
          multiplier = apyMuList[0].multiplier
          liquidity = res.bTokensLiq.filter(p => p.bTokenId === nowTokenId);
          liquidity = liquidity[0].liquidity;
        } else if (version !== 'v2_lite_open' && version !== 'v3' && version !== 'v3_lite') {
          apy = res.bTokensApy[0].apy
          liquidity = res.bTokensLiq[0].liquidity
        } else if (version === 'v3' || version === 'v3_lite') {
          apyMuList = res.bTokensApy.filter(p => p.bTokenSymbol === nowToken);
          if (apyMuList.length) {
            apy = apyMuList[0].apy;
            supplyApy = apyMuList[0].supplyApy
            xvsApy = apyMuList[0].xvsApy
            multiplier = apyMuList[0].multiplier
          }
          liquidity = res.bTokensLiq.filter(p => p.bTokenSymbol === nowToken);
          if (liquidity.length) {
            liquidity = liquidity[0].liquidity;
          }
        }
        info = {
          total: liquidity,
          apy: (+apy) * 100,
          supplyApy: +(supplyApy) * 100,
          xvsApy: +(xvsApy) * 100,
          multiplier: multiplier,
        }
        if (wallet.isConnected()) {
          let res = await poolConfig.getLiquidityInfo(wallet, address, poolConfig.version, poolConfig.isLp, type, nowToken, nowTokenId)
          info = res;
          if (version === 'v3' || version === 'v3_lite') {
            info['supplyApy'] = +(supplyApy) * 100
            info['xvsApy'] = +(xvsApy) * 100;
          }
          info['apy'] = (+apy) * 100;
          info['multiplier'] = multiplier
        }
        setPoolInfo(info)
      }

    }
  }

  const getIsStaked = async () => {
    if (wallet.isConnected() && poolConfig) {
      let res = await poolConfig.getIsStaked(wallet, poolConfig)
      let arr = bTokenList.map((item) => {
        if (version === 'v2' && type !== 'option') {
          item.staked = res.bTokens.filter(p => p.bTokenId === item.bTokenId)[0].isStaked
        } else if (version === 'v3' || version === 'v3_lite') {
          let stakeLi = res.bTokens.filter(p => p.bTokenSymbol === item.bTokenSymbol)
          item.staked = stakeLi.length ? stakeLi[0].isStaked : false

        } else {
          item.staked = res.isStaked
        }
        return item
      })
      let obj = [];
      bTokenList.forEach((item, index) => {
        if (item.staked) {
          obj.push(item);
          bTokenList.splice(index, 1)
          return;
        }
      })
      bTokenList.unshift(...obj)
      if (bTokenList.length) {
        let bToken0 = bTokenList[0].bTokenSymbol
        if (isCakeLP(address)) {
          bToken0 = 'PANCAKE-LP'
        } else if (isSushiLP(address)) {
          bToken0 = 'DERI-USDT SLP'
        }
        let bTokenId = bTokenList[0].bTokenId
        setNowToken(bToken0)
        setNowTokenId(bTokenId)
      }

    }
  }
  const getVenusEarned = async () => {
    if (poolConfig) {
      let res = await poolConfig.getVenusEarned(wallet, address)
      res = +res
      setXvsNum(res)
    }
  }

  const checkoutBaseToken = async (bTokenId, bTokenSymbol) => {
    let now;
    if (version !== 'v3' && version !== 'v3_lite') {
      now = poolConfig.bTokens.filter(b => b.bTokenId === bTokenId)
    } else {
      now = poolConfig.bTokens.filter(b => b.bTokenSymbol === bTokenSymbol)
    }
    setPoolInfo({})
    setNowTokenId(bTokenId)
    setWalletBalance('')
    setNowToken(now[0].bTokenSymbol)
    setNowIsStaked()
  }

  useEffect(() => {
    if (wallet.detail.chainId !== chainId) {
      wallet.switchNetwork(config[chainId])
    }
  }, [wallet.detail])
  useEffect(() => {
    load()
  }, [])

  useEffect(() => {
    setShowBTokenList(bTokenList)
  }, [bTokenList])
  useEffect(() => {
    if (bTokenList.length && (nowToken || nowTokenId)) {
      let apyObj = {}
      if (version !== 'v2') {
        let apyList = bTokenList.filter(p => p.bTokenSymbol === nowToken)
        apyObj = apyList[0]
      } else if (version === 'v2') {
        let apyList = bTokenList.filter(p => p.nowTokenId === nowTokenId)
        apyObj = apyList[0]
      }
      setNowApy(apyObj)
      let now;
      if (version === 'v2' && type !== 'option') {
        if (nowTokenId) {
          now = bTokenList.filter(p => p.bTokenId === nowTokenId)[0].staked
        }
      } else if(type !== 'lp') {
        now = bTokenList.filter(p => p.bTokenSymbol === nowToken)[0].staked
      }
      setNowIsStaked(now)
    }

  }, [bTokenList, nowToken, nowTokenId])

  useEffect(() => {
    getPoolInfo()
  }, [poolConfig, bTokenList])
  useEffect(() => {
    if (wallet.isConnected()) {
      if (version === 'v3' || version === 'v3_lite') {
        let interval = null;
        getVenusEarned()
        interval = window.setInterval(() => {
          getVenusEarned()
        }, 1000 * 60 * 3);
        return () => {
          interval && clearInterval(interval);
        };
      }
    }
  }, [wallet, wallet.detail, poolConfig])
  useEffect(() => {
    getLiquidityInfo()
    getWalletBalance()
  }, [nowTokenId, poolConfig, wallet.detail.account, nowToken])
  useEffect(() => {
    getIsStaked()
  }, [poolConfig, wallet.detail.account, bTokenList])
  return (
    <div className='pool-detail'>
      <div className='back-pool'>
        <Link to={(address === '0x08aD0E0b4458183758fC5b9b6D27c372965fB780' || address === '0x66f501dda450C8978c4A1115D7b2A7FAa7702F05' || address === '0x574022307e60bE1f07da6Ec1cB8fE23d426e5831') ? '/retired' : '/pool'}>
          <div className='icon-back'>
            <img alt='' src={backIcon} />
            <span>{lang['back']}</span>
          </div>
        </Link>
      </div>
      <div className='pool-deatail-info'>
        <div className='pool-detail-basetoken-info'>
          <div className='pool-info-text'>
            <div className='pool-network-type'>
              <div className='network-logo'>
                <img alt='' src={networkLogo(chainId)} />
              </div>
              <div className='pool-type-network-text'>
                <div className='pool-type-text'>
                  {lang[poolConfig.type]} {lang[poolConfig.zone]}
                </div>
                <div className='pool-network-text'>
                  {networkText}
                </div>
              </div>
            </div>
            <div className='pool-des'>
              {type !== 'lp' && lang['amm-pool-des']}
              {version === 'clp' && <>
                {lang['deri-pancake-pool']}
              </>}
              {version === 'slp' && <>
                {lang['deri-sushi-pool']}
              </>}
              <br></br>
              {type !== 'lp' ? <a rel='norelerror' target='_blank' href='https://docs.deri.finance/library/faqs/mining-faq#is-the-amm-liquidity-mining-on-deri-pools-risk-free'><span className='learn-more'> {lang['learn-more']}</span></a> : <a rel='norelerror' target='_blank' href='https://docs.deri.finance/library/faqs/mining-faq#is-dex-liquidity-mining-on-sushiswap-sushiswap-onsen-and-pancakeswap-deri-risk-free'><span className='learn-more'> {lang['learn-more']}</span></a>}
            </div>
          </div>
          <div className='select-basetoken-info'>
            <div className='select-coin-title-search'>
              <div className='select-coin-title'> {lang['select-the-coin']}</div>
              <div className='search-box'>
                <Search list={bTokenList} searchName='bTokenSymbol' onChange={(lsit) => setShowBTokenList(lsit)} />
              </div>
            </div>
            <div className='coin-search'>
              <div className='slide-icon left'>
                <Icon token='arrow-left' width='24' height='24' />
              </div>

              <div className={(version === 'v3' || version === 'v3_lite') ? "coin-list some-coin" : "coin-list"}>

                {type !== 'lp' && version !== 'v2_lite_open' && <>
                  {showBTokenList.map((item, index) =>
                  (<div className={nowToken === item.bTokenSymbol ? 'coin-box checked-base' : 'coin-box'} onClick={() => { checkoutBaseToken(item.bTokenId, item.bTokenSymbol) }}>
                    <div className='base-apy-info'>
                      <div className={`btoken-logo ${item.bTokenSymbol}`}></div>
                      <div className='apy-base-staked'>
                        <div className='base-name'>{item.bTokenSymbol}</div>
                        <div className='base-apy'>
                          {lang['apy']} &nbsp; <DeriNumberFormat width='50px' value={item.apy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />
                          {(version === 'v3' || version === 'v3_lite') && <>
                            <span> +  <DeriNumberFormat width='50px' value={item.supplyApy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />  + </span>
                            <DeriNumberFormat width='50px' value={item.xvsApy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />
                          </>}
                        </div>
                      </div>
                    </div>
                    {item.staked && <img src={stakedLogo} alt='' />}

                  </div>)
                  )}
                </>}
                {version === 'v2_lite_open' && <>
                  {showBTokenList.map((item, index) =>
                  (<div className='coin-box checked-base' onClick={() => { checkoutBaseToken(item.bTokenId) }}>
                    <div className='base-apy-info'>
                      <div className={`btoken-logo ${item.bTokenSymbol}`}>
                        <img alt='' onError={onImgError} src={`https://raw.githubusercontent.com/deri-finance/deri-open-zone/main/img/${item.bTokenSymbol}.png`} />
                      </div>
                      <div className='apy-base-staked'>
                        <div className='base-name'>{item.bTokenSymbol}</div>
                      </div>
                    </div>
                    {item.staked && <div className='stake'>
                      <img src={stakedLogo} alt='' />
                      {lang['staked']}
                    </div>}
                  </div>)
                  )}
                </>}
                {type === 'lp' && bTokenList.length && <>
                  <div className='coin-box checked-base'>
                    <div className='base-apy-info'>
                      <div className='lp-btoken-logo'>
                        <div className={`btoken-logo ${bTokenList[0].bTokenSymbol}`}></div>
                        <span className='conect-logo'>+</span>
                        <div className={`btoken-logo ${bTokenList[1].bTokenSymbol}`}></div>
                      </div>
                      <div className='apy-base-staked'>
                        <div className='base-name'>{nowToken}</div>
                        <div className='base-apy'>
                          {isCakeLP(address) && <>
                            {lang['apy']} &nbsp; <DeriNumberFormat value={bTokenList[1].apy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />
                          </>}
                          {isSushiLP(address) && <>
                            <UnderlineText text={`${lang['apy']} ${(+(bTokenList[1].apy)).toFixed(2)} %`} tip={lang['deri-apy']} />
            &nbsp; + &nbsp; <UnderlineText text={`${lang['apy']}  ${(+(bTokenList[1].lpApy)).toFixed(2)} %`} tip={lang['sushi-apy']} />
                          </>}
                        </div>
                      </div>
                    </div>
                  </div>
                </>}

              </div>
              <div className='slide-icon right'>
                <Icon token='arrow-right' width='24' height='24' />
              </div>
            </div>

          </div>
        </div>
        <div className='pool-detail-add-remove-info'>
          <div className='mining-deri-liquidity'>
            <div className='btoken-liquidity'>
              {type !== 'lp' && <>
                <div className={`btoken-logo ${nowToken}`}>
                  {version === 'v2_lite_open' && <>
                    <img alt='' onError={onImgError} src={`https://raw.githubusercontent.com/deri-finance/deri-open-zone/main/img/${nowToken}.png`} />
                  </>}
                </div>
                <div className='manage-stake'>
                  <div className='manage'>
                    {lang['manage']} {nowToken} {lang['liquidity']}
                  </div>
                  {nowIsStaked && <div className='stake'>
                    <img src={stakedLogo} alt='' />
                    {lang['staked']}
                  </div>}
                </div>
              </>}

              {type === 'lp' && bTokenList.length && <>
                <div className='lp-btoken-logo'>
                  <div className={`btoken-logo ${bTokenList[0].bTokenSymbol}`}></div>
                  <span className='conect-logo'>+</span>
                  <div className={`btoken-logo ${bTokenList[1].bTokenSymbol}`}></div>
                </div>
                <div className='manage-stake'>
                  <div className='manage'>
                    {lang['manage']} {nowToken} {lang['liquidity']}
                  </div>
                  {nowIsStaked && <div className='stake'>
                    <img src={stakedLogo} alt='' />
                    {lang['staked']}
                  </div>}
                </div>
              </>}
            </div>
            <div className='deri-current-epoch'>
              {version !== 'v2_lite_open' && <Claim poolShow={false} lang={lang} wallet={wallet} />}
            </div>
          </div>
          <div className='mining-add-remove-liquidity'>
            <div className='pool-info'>
              <div className='pool-info-title'>
                <div className='pool-info-title-text'>
                  {lang['pool-info']}
                </div>
                <div className='down-up'>
                  {isShowInfo === 'show' && <span onClick={() => setIsShowInfo('hide')}>
                    <img className='up-icon' alt='' src={downIcon} />
                  </span>}
                  {isShowInfo === 'hide' && <span onClick={() => setIsShowInfo('show')}>
                    <img alt='' src={downIcon} />
                  </span>}
                </div>
              </div>
              <div className={`'pool-info-text' ${isShowInfo}`}>
                <div className='pool-info-text-one'>
                  {version !== 'v2_lite_open' && <div className='info-box'>
                    <div className='info-box-title'>{lang['apy']}</div>
                    <div className='info-box-num'>
                      {(version !== 'v3' && version !== 'v3_lite') && <DeriNumberFormat value={poolInfo.apy} displayType='text' decimalScale={2} suffix='%' />}
                      {(version === 'v3' || version === 'v3_lite') && <>
                        <UnderlineText text={<DeriNumberFormat width='50px' value={nowApy.apy} displayType='text' decimalScale={2} suffix='%' />} tip={lang['deri-apy']} />
                        &nbsp; +  &nbsp;  <UnderlineText text={<DeriNumberFormat width='50px' value={nowApy.supplyApy} displayType='text' decimalScale={2} suffix='%' />} tip={lang['venus-suppy-apy']} />  &nbsp; +  &nbsp;
                          <UnderlineText text={<DeriNumberFormat width='50px' value={nowApy.xvsApy} displayType='text' decimalScale={2} suffix='%' />} tip={lang['xvs-distribution-apy']} />
                      </>}
                    </div>
                  </div>}
                  <div className='info-box'>
                    <div className='info-box-title'>{lang['pool-liquidity']}</div>
                    <div className='info-box-num'>
                      <DeriNumberFormat value={poolInfo.total} displayType='text' decimalScale={2} thousandSeparator={true} /> {(version === 'v3' || version === 'v3_lite') ? 'BUSD' : nowToken}
                    </div>
                  </div>
                  {(version === 'v2' || version === 'v3' || version === 'v3_lite') && !(type === 'option' && version === 'v2') && <div className='info-box'>
                    <div className='info-box-title'>{lang['multiplier']}</div>
                    <div className='info-box-num'>{poolInfo.multiplier}</div>
                  </div>}
                  <div className='info-box'>
                    <div className='info-box-title'>{lang['my-liquidity-percentage']}</div>
                    <div className='info-box-num'><DeriNumberFormat allowZero={true} value={poolInfo.percent} decimalScale={2} suffix={'%'} /></div>
                  </div>
                  {((version !== 'v2' && version !== 'v3' && version !== 'v3_lite') || (version === 'v2' && type === 'option')) && <>
                    <div className='info-box'>
                      <div className='info-box-title'>{lang['liquidity-share-value']}</div>
                      <div className='info-box-num'><DeriNumberFormat allowZero={true} decimalScale={6} value={poolInfo.shareValue} thousandSeparator={true} /> {nowToken}</div>
                    </div>
                  </>}
                  <div className='info-box'>
                    <div className='info-box-title'>{lang['staked-balance']}</div>
                    <div className='info-box-num'>
                      <DeriNumberFormat allowZero={true} value={poolInfo.formatShares} decimalScale={2} />
                      {(version === 'v2' || version === 'v3' || version === 'v3_lite') && !(type === 'option' && version === 'v2') ? " " + nowToken : " " + lang['shares']}
                    </div>
                  </div>
                  {((version !== 'v2' && version !== 'v3' && version !== 'v3_lite') || (version === 'v2' && type === 'option')) &&
                    <div className='info-box'>
                      <div className='info-box-title'></div>
                      <div className='info-box-num'><DeriNumberFormat allowZero={true} decimalScale={2} value={poolInfo.values} thousandSeparator={true} /> {nowToken}</div>
                    </div>
                  }
                  {(version === 'v2' || version === 'v3' || version === 'v3_lite') && !(type === 'option' && version === 'v2') && <div className='info-box'>
                    <div className='info-box-title'>
                      <UnderlineText multiline={true} text={lang['mining-pnl']} tip={lang['mining-pnl-tip']} />
                    </div>
                    <div className='info-box-num'>
                      <DeriNumberFormat allowZero={true} prefix=' ' value={poolInfo.swappedPnl} decimalScale={2} /> {nowToken}
                    </div>
                  </div>}
                  {(version === 'v3' || version === 'v3_lite') && <div className='info-box'>
                    <div className='info-box-title'>{lang['xvs-earned']}</div>
                    <div className='info-box-num collect-box'>
                      <DeriNumberFormat value={xvsNum} decimalScale={2} /> <Button onClick={Collect} className='collect' label={lang['collect']} />
                    </div>
                  </div>
                  }
                </div>
                <div className='pool-info-text-two'>
                  {type !== 'lp' && <div className='info-box'>
                    <div className='info-box-title'>{lang['symbols']}</div>
                    <div className='info-box-num symbols'>
                      {poolConfig.zone === 'main' ? symbols : <UnderlineText tip={symbols}>
                        {symbols}
                      </UnderlineText>}
                    </div>
                  </div>}
                  <div className='info-box'>
                    <div className='info-box-title'>{lang['contract-address']}</div>
                    <div className='info-box-num'>
                      <a target='_blank' rel='noreferrer' href={`${chainConfig[chainId] && chainConfig[chainId]['viewUrl']}/address/${address}`}>
                        {formatAddress(address)}
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className='add-remove-liquidity'>
              <Tab tabSet={tabSet} width={379} size={18} inactiveBgColor='#0c192d' icon='' borderBottom={true} divider={true} />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet', 'poolManager')(observer(Detail))